<template>
    <m-navbar
        :title="title"
        placeholder
        @left-click="handleClick"
    />
</template>

<script lang="ts" setup>
const handleClick = () => {
    const pages = getCurrentPages()

    if (pages.length > 1) {
        uni.$m.route({ type: 'back'})
    } else {
        uni.$m.route({
            type: 'to',
            url: 'pages/index/index'
        })
    }
}

const props = defineProps({
    title: String,
    default: ''
})
</script>

